import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validasiTransaksiPenjualan from "../../Validations/validasiTransaksiPenjualan";
import { getListBarang } from "../../actions/kodeBarangAction";
import { setCameraURI } from "../../actions/cameraAction";
import { renderField } from "../../renderField";
import {  resizeFile } from "../../helper";
import { setGambarL } from "../../actions/PackingListAction";
import CameraPhoto, { FACING_MODES } from "jslib-html5-camera-photo";


class FormGambarPackingL extends Component {
  constructor(props) {
    super(props);
    this.cameraPhoto = null;
    this.videoRef = React.createRef();
    this.state = {
      kodeBarang: [],
      files: [],
      dataUri: []
    };
  }


  componentWillUnmount() {
    this.cameraPhoto
      .stopCamera()
      .then(() => {
        console.log("Camera stoped!");
      })
      .catch((error) => {
        console.log("No camera to stop!:", error);
      });
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    this.cameraPhoto = new CameraPhoto(this.videoRef.current);
    setTimeout(() => {
      this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {
        width: 720,
        height: 720,
      });
    }, 1000);
  }

  takePhoto() {
    const config = {
      sizeFactor: 1,
      imageCompression: 0.1,
      isImageMirror: false,
    };
    let foto = this.props.listGambarL;
    foto.push({"lokasi_gambar" : this.cameraPhoto.getDataUri(config)})
    console.log(JSON.stringify(foto));
    this.setState({ dataUri: foto });
    this.props.dispatch(setGambarL(foto))
  }

  stopCamera() {
    this.cameraPhoto
      .stopCamera()
      .then(() => {
        console.log("Camera stoped!");
      })
      .catch((error) => {
        console.log("No camera to stop!:", error);
      });
  }

  handleUpload = async (e) => {

    const file = e.target.files;
    this.setState({
      files: [...this.state.files, ...e.target.files]
    })
    let listImage = this.props.listGambarL;
    for (let i = 0; i < file.length; i++) {
      let reader = new FileReader();
      reader.onload = this.handleReaderLoaded;
      let compressed = await resizeFile(file[i]);
      reader.readAsDataURL(file[i]);
      if (reader !== undefined && file !== undefined) {
        reader.onloadend = () => {
          listImage.push({ "lokasi_gambar": compressed })
          this.props.dispatch(setCameraURI(listImage));
          this.props.dispatch(setGambarL(listImage));
          this.setState({
            count: i
          })
        };
      }
    }
    console.log(this.state.files);
    this.props.change("gambar", listImage)
  };

  handleReaderLoaded = (readerEvt) => {
    let binaryString = readerEvt.target.result;
    this.setState({
      imageBase64: btoa(binaryString),
    });
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12 row">
          <div className="col-lg-12 row justify-content-center">
            <div className="col-lg-6">
              <video
                ref={this.videoRef}
                autoPlay="true"
                width="100%"
                height="95%"
              />
            </div>
          </div>
          <div className="col-lg-12 row justify-content-center mb-2">
            <div className="col-lg-3">
              <div className="text-center">
                <button
                  className="btn btn-primary btn-block"
                  type="button"
                  onClick={() => this.takePhoto()}
                >
                  Ambil Gambar
                </button>
              </div>
            </div>
            <div className="col-lg-3">
              <div className="text-center">
                <button
                  className="btn btn-danger btn-block"
                  type="button"
                  onClick={() =>
                    {this.setState({
                      dataUri: [],
                    })
                    this.props.dispatch(setGambarL([]))
                    this.props.dispatch(setCameraURI([]))}
                  }
                >
                  Reset Gambar
                </button>
              </div>
            </div>
          </div>
          <div className="col-lg-12 row justify-content-center">
            {
              this.props.listGambarL?.map((data) => (
                <div className="col-lg-3 mb-1 mt-1 ml-1 mr-1">
                  <img
                    alt="imgCamera"
                    src={data?.lokasi_gambar}
                    width="100%"
                    height="100%"
                  />
                </div>
              ))
            }
          </div>
          <div className="col-lg-12">
            <h3>Silahkan Pilih Gambar</h3>
          </div>
          <div className="col-lg-12">
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="gambar"
                label="Gambar"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
          </div>
          <div className="col-lg-3">
            <div class="input-group mt-2">
              <div class="input-group-prepend">
                <button
                  class="btn btn-danger"
                  type="button"
                  onClick={() => {
                    this.setState({
                      files: []
                    });
                    this.props.dispatch(setGambarL([]))
                    this.props.dispatch(setCameraURI([]))
                  }}
                >
                  Hapus
                </button>
              </div>
              <div class="custom-file">
                <input
                  className="custom-file-input"
                  type="file"
                  multiple
                  name="avatar"
                  id="file"
                  accept=".jpef, .png, .jpg"
                  onChange={(data) => this.handleUpload(data)}
                  src={this.state.image}
                />
                <label class="custom-file-label" for="inputGroupFile03">
                  Choose file
                </label>
              </div>
            </div>
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormGambarPackingL = reduxForm({
  form: "FormGambarPackingL",
  enableReinitialize: true,
  validate: validasiTransaksiPenjualan,
})(FormGambarPackingL);
export default connect((state) => {
  return {
    cameraURI: state.provinsi.cameraURI,
    listGambarL: state.provinsi.listGambarL || []
  };
})(FormGambarPackingL);
