import PrintNotaGambarReturn from "../Stocking/Component/PrintNotaGambarReturn";
import FormGambarPackingL from "./Component/FormGambarPackingL";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormTransaksiReturn = lazy(() =>
  import("./Component/FormTranskasiReturn")
);
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    ReturnPenjualan: state.provinsi.returnPenjualan,
    listCustomer: state.provinsi.customer,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    totalNW: state.provinsi.totalNW,
    sumNetto: state.perhitungan.sumNetto,
    sumBruto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumP: state.perhitungan.sumP,
    sumK: state.perhitungan.sumK,
    sumQty: state.perhitungan.sumNetto,
    sumGross: state.perhitungan.sumGross,
    sumRp: state.perhitungan.sumRp,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
    kodeBarang: state.provinsi.kodeBarang,
  };
};
class ReturnPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kadar_cetak",
          text: "Cap\nKode",
        },
        {
          dataField: "qty",
          text: "Qty",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "plastik",
          text: "P",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "kulit",
          text: "K",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "kadar_jual",
          text: "Kode\nTukar",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc\n(Gr)",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto\nDisc(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(Rp)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, row.netto_disc)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      discGlobalGr: 0,
      discGlobalPr: 0,
      totalNWExt: 0,
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      tanggal: "",
      data: "",
      kodeCustomer: "",
    };
  }

  deleteRow(rowIndex, dihapus) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("ReturnPenjualan", JSON.stringify(array));
        var awal = localStorage.getItem("GrandTotalNetto");
        var hasil = parseFloat(awal) - parseFloat(dihapus);
        localStorage.setItem("GrandTotalNetto", parseFloat(hasil).toFixed(3));
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        })
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListReturnPenjualan()))
          .then(() =>
            this.setState({
              GrandTotalNetto: (
                parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
              ).toFixed(3),
            })
          );
      }
    });
  }
  componentDidMount() {
    localStorage.removeItem("nota_return");
    localStorage.removeItem("ReturnPenjualan");
    localStorage.removeItem("ReturnPenjualan");
    localStorage.removeItem("GrandTotalNetto");
    localStorage.removeItem("nota_gambar");
    this.props.dispatch(getLokasi());
    this.props.dispatch(getsalesmanList());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getListReturnPenjualan());
    this.props.dispatch(getTotalNW());

    this.setState({
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
      ).toFixed(3),
      noBon: getBon(),
    });
  }

  handleSubmit(data) {
    let param = data.kodeBarang;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    if (param === undefined) {
      var urutan = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
      var simpan_nota_1 = JSON.parse(localStorage.getItem("nota_return")) || [];
      var posisi = simpan_nota_1.findIndex(
        (data) => data.kode_kategori === data.kodeKategori
      );
      if (posisi !== -1) {
        var nota_1 = {
          netto_disc: (
            parseFloat( 0) +
            parseFloat(simpan_nota_1[posisi].netto_disc)
          ).toFixed(3),
          netto: (
            parseFloat(data.bruto || 0) +
            parseFloat(simpan_nota_1[posisi].netto)
          ).toFixed(3),
          ongkos_rp: (
            parseFloat(0) +
            simpan_nota_1[posisi].ongkos_rp
          ).toLocaleString("id-ID"), //ongkos
          disc: (
            parseFloat(0) +
            parseFloat(simpan_nota_1[posisi].disc)
          ).toFixed(3),
          bruto: (
            parseFloat(data.bruto || 0) +
            parseFloat(simpan_nota_1[posisi].bruto)
          ).toFixed(3), //berat
          kode_kategori: "-",
          kode_tukar:  100,
          keterangan: "BUKAN BARANG AYU",
          jumlah_barang_set: 0,
          total_berat_barang_set: 0,
          kode_barcode_set: "-",
          harga_jual:
            data.harga_jual === "undefined"
              ? 0
              : parseFloat(data.harga_jual || 0) * parseFloat(data.qty || 0) +
                parseFloat(simpan_nota_1[posisi].harga_jual),
        };
        simpan_nota_1.push(nota_1);
        simpan_nota_1.splice(posisi, 1);
        localStorage.setItem("nota_return", JSON.stringify(simpan_nota_1));
      } else {
        var nota_1_else = {
          netto_disc: parseFloat(data.nettoDiscGlobalExt || 0).toFixed(3),
          netto: parseFloat(data.netto || 0).toFixed(3), //murni
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          disc: data.discGlobalGr || 0,
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
          kode_kategori: "-",
          kode_tukar: 100,
          keterangan: "BUKAN BARANG AYU",
          disc_persen:  0,
          jumlah_barang_set: 0,
          total_berat_barang_set: 0,
          kode_barcode_set: "-",
          harga_jual:
            data.harga_jual === "undefined"
              ? 0
              : parseFloat(data.harga_jual || 0) * parseFloat(data.qty || 0),
        };
        simpan_nota_1.push(nota_1_else);
        localStorage.setItem("nota_return", JSON.stringify(simpan_nota_1));
      }
      var simpan = {
        no_urut: (urutan.length || 0) + 1,
        netto_disc: data.bruto || 0,
        gross: parseFloat(data.bruto) + parseFloat(data.pkg) || 0, //berat + bungkus
        netto: parseFloat(data.bruto) || 0, //murni
        keterangan: "BUKAN BARANG AYU",
        kode_barcode: data.kode_barcode || "-",
        qty: data.qty || 1,
        kulit: parseFloat(data.k) || 0,
        ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp) || 0, //ongkos
        kadar_cetak: "100",
        disc_persen:  0,
        kadar_jual: 100, //harga%
        kadar_modal: 100, //harga%
        kode_jenis: "-", //kodebarang
        plastik: parseFloat(data.p) || 0,
        disc: 0,
        pkg: parseFloat(data.pkg) || 0,
        bruto: parseFloat(data.bruto) || 0, //berat
        harga_jual: 0,
      };
      var array = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
      array.push(simpan);
      var GrandTotalNetto = array
        .map((list) => parseFloat(list.netto_disc))
        .reduce((a, b) => a + b, 0);
      localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
      localStorage.setItem("ReturnPenjualan", JSON.stringify(array));
      Swal.fire({
        position: "top-end",
        icon: "success",
        text: "Data Berhasil Ditambahkan!",
        showConfirmButton: false,
        timer: 1500,
      });
      this.props.dispatch(getListReturnPenjualan());
      this.props.dispatch(getTotalNW());
      this.props.dispatch(reset("FormTransaksiPenjualan"));

      this.props.dispatch(getNetto("ReturnPenjualan"));
      this.setState({
        discGlobalGr:
          (parseFloat(0) / 100) *
          parseFloat(localStorage.getItem("GrandTotalNetto")),
        discGlobalPr: 0,
        GrandTotalNetto: (
          parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
        ).toFixed(3),
      });
    } else {
      AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kodeBarang}`).then(
        (res) => {
          var urutan =
            JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
          var simpan_nota_1 =
            JSON.parse(localStorage.getItem("nota_return")) || [];
          var nota_gambar =
            JSON.parse(localStorage.getItem("nota_gambar")) || [];
          var posisi = simpan_nota_1.findIndex(
            (data) => data.kode_kategori === data.kodeKategori
          );
          nota_gambar.push({ lokasi_gambar: res.data?.image });
          localStorage.setItem("nota_gambar", JSON.stringify(nota_gambar));
          if (posisi !== -1) {
            var nota_1 = {
              netto_disc: (
                parseFloat(data.nettoDiscGlobalExt || 0) +
                parseFloat(simpan_nota_1[posisi].netto_disc)
              ).toFixed(3),
              netto: (
                parseFloat(data.netto || 0) +
                parseFloat(simpan_nota_1[posisi].netto)
              ).toFixed(3),
              ongkos_rp: (
                parseFloat(data.rp === undefined ? 0 : data.rp) +
                simpan_nota_1[posisi].ongkos_rp
              ).toLocaleString("id-ID"), //ongkos
              disc: (
                parseFloat(data.discGlobalGr || 0) +
                parseFloat(simpan_nota_1[posisi].disc)
              ).toFixed(3),
              bruto: (
                parseFloat(data.bruto || 0) +
                parseFloat(simpan_nota_1[posisi].bruto)
              ).toFixed(3), //berat
              kode_kategori: data.kodeKategori,
              kode_tukar: data.kadarTransaksi || 0,
              keterangan: data.keterangan || "-",
              jumlah_barang_set: 0,
              total_berat_barang_set: 0,
              kode_barcode_set: "-",
              harga_jual:
                data.harga_jual === "undefined"
                  ? 0
                  : parseFloat(data.harga_jual || 0) *
                      parseFloat(data.qty || 0) +
                    parseFloat(simpan_nota_1[posisi].harga_jual),
            };
            simpan_nota_1.push(nota_1);
            simpan_nota_1.splice(posisi, 1);
            localStorage.setItem("nota_return", JSON.stringify(simpan_nota_1));
          } else {
            var nota_1_else = {
              netto_disc: parseFloat(data.nettoDiscGlobalExt || 0).toFixed(3),
              netto: parseFloat(data.netto || 0).toFixed(3), //murni
              ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
              disc: data.discGlobalGr || 0,
              bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
              kode_kategori: data.kodeKategori || 0,
              kode_tukar: data.kadarTransaksi || 0,
              keterangan: data.keterangan,
              disc_persen: data.discGlobalPr || 0,
              jumlah_barang_set: 0,
              total_berat_barang_set: 0,
              kode_barcode_set: "-",
              harga_jual:
                data.harga_jual === "undefined"
                  ? 0
                  : parseFloat(data.harga_jual || 0) *
                    parseFloat(data.qty || 0),
            };
            simpan_nota_1.push(nota_1_else);
            localStorage.setItem("nota_return", JSON.stringify(simpan_nota_1));
          }
          var simpan = {
            no_urut: (urutan.length || 0) + 1,
            netto_disc: data.nettoDiscGlobalExt || 0,
            gross: parseFloat(data.gross) || 0, //berat + bungkus
            netto: parseFloat(data.netto) || 0, //murni
            keterangan: data.keterangan,
            kode_barcode: data.kode_barcode || "-",
            qty: data.qty || 1,
            kulit: parseFloat(data.k) || 0,
            ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp) || 0, //ongkos
            kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
            disc_persen: data.discGlobalPr || 0,
            kadar_jual: data.kadarTransaksi || 0, //harga%
            kadar_modal: data.kadarTransaksi || 0, //harga%
            kode_jenis: hasil[0].kode_jenis || 0, //kodebarang
            plastik: parseFloat(data.p) || 0,
            disc: data.discGlobalGr || 0,
            pkg: parseFloat(data.pkg) || 0,
            bruto: parseFloat(data.bruto) || 0, //berat
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          var array = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
          array.push(simpan);
          var GrandTotalNetto = array
            .map((list) => parseFloat(list.netto_disc))
            .reduce((a, b) => a + b, 0);
          localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
          localStorage.setItem("ReturnPenjualan", JSON.stringify(array));
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Ditambahkan!",
            showConfirmButton: false,
            timer: 1500,
          });
          this.props.dispatch(getListReturnPenjualan());
          this.props.dispatch(getTotalNW());
          this.props.dispatch(reset("FormTransaksiPenjualan"));

          this.props.dispatch(getNetto("ReturnPenjualan"));
          this.setState({
            discGlobalGr:
              (parseFloat(0) / 100) *
              parseFloat(localStorage.getItem("GrandTotalNetto")),
            discGlobalPr: 0,
            GrandTotalNetto: (
              parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
            ).toFixed(3),
          });
        }
      );
    }
  }

  transaksiPenjualanSubmit() {
    if (localStorage.getItem("ReturnPenjualan") === null) {
      warning("Barang masih kosong, Silahkan tambah barang");
      return false;
    } else if (this.state.namaToko === "") {
      warning("Mohon pilih toko");
      return false;
    } else if (this.state.discGlobalPr === "") {
      warning("Mohon isi Discount");
      return false;
    } else if (this.state.discGlobalGr === "") {
      warning("Mohon isi Discount Gr");
      return false;
    } else if (this.state.totalNWExt === 0) {
      warning("Mohon isi Grand Total");
      return false;
    }
    var simpan = {
      no_bon: "AS/RTR-" + this.state.noBon.toUpperCase(),
      no_retur: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal || getToday(),
      kode_customer: this.state.kodeCustomer.toUpperCase(),
      detail_barang: JSON.parse(localStorage.getItem("ReturnPenjualan")),
      total_netto: this.props.totalNW,
      disc_global_persen: this.state.discGlobalPr,
      disc_global: this.state.discGlobalGr,
      total_netto_disc: this.state.GrandTotalNetto,
      total_netto_disc_ext: this.state.totalNWExt,
      image : JSON.parse(localStorage.getItem("nota_gambar") || "[]")
    };
    console.log(JSON.stringify(simpan));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    const tableRowsNonAyu = [];
    var table = JSON.parse(localStorage.getItem("nota_return")).filter((x) => x.kode_kategori !== "-");
    var tableNonAyu = JSON.parse(localStorage.getItem("nota_return")).filter((x) => x.kode_kategori === "-");
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_kategori,
        ticket.keterangan,
        { content: ticket.jumlah_barang_set, styles: { halign: "right" } },
        {
          content: formatGram(ticket.total_berat_barang_set),
          styles: { halign: "right" },
        },
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        { content: formatGram(ticket.kode_tukar), styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        { content: ticket.disc, styles: { halign: "right" } },
        { content: formatGram(ticket.netto_disc), styles: { halign: "right" } },
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    tableNonAyu.forEach((ticket, i) => {
      const ticketDataNonAyu = [
        ++i,
        "-",
        ticket.keterangan,
        { content: ticket.jumlah_barang_set, styles: { halign: "right" } },
        {
          content: formatGram(ticket.total_berat_barang_set),
          styles: { halign: "right" },
        },
        { content: formatGram(ticket.bruto), styles: { halign: "right" } },
        { content: formatGram(ticket.kode_tukar), styles: { halign: "right" } },
        { content: formatGram(ticket.netto, 3), styles: { halign: "right" } },
        { content: ticket.disc, styles: { halign: "right" } },
        { content: formatGram(ticket.netto_disc), styles: { halign: "right" } },
      ];
      // push each tickcet's info into a row
      tableRowsNonAyu.push(ticketDataNonAyu);
    });
    let footer = [
      "",
      "",
      "",
      "",
      "",
      {
        content: formatGram(
          table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      "",
      {
        content: formatGram(
          table
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0),
          3
        ),
        styles: { halign: "right" },
      },
      {
        content: formatGram(
          table.map((list) => parseFloat(list.disc)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      {
        content: formatGram(this.state.totalNWExt, 3),
        styles: { halign: "right" },
      },
    ];
    let footerNonAyu = [
      "",
      "",
      "",
      "",
      "",
      {
        content: formatGram(
          tableNonAyu.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
        ),
        styles: { halign: "right" },
      },
      "",
      {
        content: formatGram(
          tableNonAyu
            .map((list) => parseFloat(list.netto))
            .reduce((a, b) => a + b, 0),
          3
        ),
        styles: { halign: "right" },
      },
      {
        content: 0,
        styles: { halign: "right" },
      },
      {
        content: 0,
        styles: { halign: "right" },
      },
    ];
    // tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      [
        "No",
        "Kode",
        "Keterangan",
        { content: "Jml. Brg\nSet", styles: { halign: "right" } },
        { content: "Total Berat\nSet", styles: { halign: "right" } },
        { content: "Berat\nKotor", styles: { halign: "right" } },
        { content: "Kode\nTukar", styles: { halign: "right" } },
        { content: "Berat\nBersih", styles: { halign: "right" } },
        { content: "RDC", styles: { halign: "right" } },
        { content: "Berat\nBersih RDC", styles: { halign: "right" } },
      ],
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("trx-customer/retur-customer", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => {
            PrintNotaGambarReturn(
              "No Nota",
              "AS/RTR-" + this.state.noBon,
              "Tanggal",
              this.state.tanggal || getToday(),
              "Customer",
              this.state.namaCustomer,
              "",
              "",
              getUserID(),
              getToday(),
              "",
              columTable,
              "Transaksi Return Penjualan",
              tableRows,
              JSON.parse(localStorage.getItem("nota_gambar")) || [],
              footer,
              tableRowsNonAyu,
              footerNonAyu
            );
            PrintNotaReturn(
              "No Nota",
              "AS/RTR-" + this.state.noBon,
              "Tanggal",
              this.state.tanggal || getToday(),
              "Customer",
              this.state.namaCustomer,
              "",
              "",
              getUserID(),
              getToday(),
              "",
              columTable,
              "Transaksi Return Penjualan",
              tableRows,
              footer,
              tableRowsNonAyu,
              footerNonAyu
            );
          })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("ReturnPenjualan"))
          .then(() => localStorage.removeItem("GrandTotalNetto"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => localStorage.removeItem("nota_gambar"))
          .then(() => localStorage.removeItem("nota_return"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListReturnPenjualan()))
          .then(() =>
            this.setState({
              noBon: "",
              namaCustomer: "",
              lokasi: "",
              sales: "",
              namaToko: "",
              tanggal: "",
              toko: "",
              GrandTotalNetto: 0,
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        {
          console.log(err);
          Swal.fire({
          position: "top-end",
          icon: "error",
          text: `${err.response.data}`,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))}
      );
    console.log(JSON.stringify(simpan));
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  inputGrandTotal = (input) => (e) => {
    var GrandNetto = this.props.sumGross - parseFloat(this.state.discGlobalGr);
    var grandTotal = e.target.value;
    console.log(GrandNetto + "-" + grandTotal);
    if (GrandNetto - grandTotal < -1) {
      warning("Selisih lebih dari 1");
      e.target.value = "";
      return false;
    } else {
      if (GrandNetto - grandTotal <= 1) {
        this.setState({
          [input]: e.target.value,
        });
      } else {
        warning("Selisih lebih dari 1");
        e.target.value = "";
        return false;
      }
    }
  };
  discount = (input) => (e) => {
    var hasil =
      (parseFloat(e.target.value) / 100) *
      parseFloat(localStorage.getItem("GrandTotalNetto"));
    this.setState({
      discGlobalGr: hasil.toFixed(3),
      discGlobalPr: e.target.value,
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto")) - hasil.toFixed(3)
      ).toFixed(3),
    });
  };
  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data, input) {
    var split = data.split("|");
    localStorage.setItem("customer_1", split[0]);
    this.setState({
      kodeCustomer: split[0],
      namaCustomer: split[1],
      namaToko: split[2],
      toko: data,
    });
  }

  showGambar(){
    this.setState({
      showModal: "showGambar"
    })
    this.props.dispatch(showModal())
  }

  showTambahBarang(){
    this.setState({
      showModal: "tambahBarang"
    })
    this.props.dispatch(showModal())
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <HeaderCard title="Return Penjualan" />
            <div className="card-body">
              <div className="row">
                <div className="col-lg-9"></div>
                {/* <div className="col-lg-3">
                  <div className="text-right">
                    <Link to="/EditForm/ReturnPenjualan">
                      <button
                        className="btn btn-warning"
                        // data-target="#editModal"
                        // data-toggle="modal"
                      >
                        Edit Transaksi Penjualan
                      </button>
                    </Link>
                  </div>
                </div> */}
                <div className="col-lg-12">
                  <br />
                </div>

                <div className="col-lg-12">
                  <form
                    id="myForm"
                    onKeyDown={(event) => {
                      return event.key !== "Enter";
                    }}
                  >
                    <div className="row">
                      <div className="col-lg-4 col-md-4">
                        <div className="form-group">
                          <label htmlFor="">Tanggal</label>
                          <input
                            name="tanggal"
                            type="date"
                            label="Tanggal"
                            className="form-control"
                            onChange={this.inputChange("tanggal")}
                            defaultValue={getToday()}
                          />
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4">
                        {/* <label htmlFor="">Nomor Bon</label>
                        <div className="input-group mb-3">
                          <div className="input-group-prepend">
                            <span
                              className="input-group-text"
                              id="basic-addon3"
                            >
                              AS/RTR-
                            </span>
                          </div>
                          <input
                            type="text"
                            className="form-control"
                            id="basic-url"
                            aria-describedby="basic-addon3"
                            onChange={this.inputChange("noBon")}
                          />
                        </div> */}
                      </div>
                      <div className="col-lg-4 col-md-4"></div>
                      {/* <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Lokasi</label>
                          <SelectSearch
                            options={this.props.listLokasi.map((obj) => {
                              var payload = {
                                value: obj.kode_lokasi,
                                name: obj.nama_lokasi,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) => this.onSubmit(data, "lokasi")}
                            value={this.state.lokasi}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Sales</label>
                          <SelectSearch
                            options={this.props.listSales.map((obj) => {
                              var payload = {
                                value: obj.kode_sales,
                                name: obj.nama_sales,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) => this.onSubmit(data, "sales")}
                            value={this.state.sales}
                          />
                        </div>
                      </div> */}
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.props.listCustomer.map((obj) => {
                              var payload = {
                                value:
                                  obj.kode_customer +
                                  "|" +
                                  obj.nama_customer +
                                  "|" +
                                  obj.nama_toko,
                                name: obj.nama_toko,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.onSubmitToko(data, "namaToko")
                            }
                            onBlur={(data) => this.onSubmit(data, "toko")}
                            value={this.state.toko}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Customer</label>
                          <input
                            name="customer"
                            type="text"
                            className="form-control"
                            label="Customer"
                            onChange={this.inputChange("namaCustomer")}
                            defaultValue={this.state.namaCustomer}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 mb-3">
                        <label htmlFor="">Total NW</label>
                        <input
                          name="totalNW"
                          type="number"
                          className="form-control"
                          min={0}
                          step={0.01}
                          label="totalNW"
                          value={this.props.totalNW}
                          readOnly={true}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Disc Global (%)</label>
                        <input
                          name="discGlobal"
                          type="number"
                          className="form-control"
                          min={0}
                          step={0.001}
                          label="discGlobal"
                          onChange={this.discount("discGlobalPr")}
                          value={this.state.discGlobalPr}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Disc (Gr)</label>
                        <input
                          name="discGlobal"
                          type="number"
                          className="form-control"
                          label="discGlobal"
                          value={this.state.discGlobalGr}
                          readOnly={true}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Grand Total Netto</label>
                        <input
                          name="totalNW"
                          type="number"
                          className="form-control"
                          label="totalNW"
                          value={this.state.GrandTotalNetto}
                          readOnly={true}
                          onClick={(e) => {
                            this.setState({ totalNWExt: e.target.value });
                            document.getElementById("totalNWExt").value =
                              e.target.value;
                          }}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Grand Total Netto Ext</label>
                        <input
                          name="totalNWExt"
                          id="totalNWExt"
                          type="text"
                          className="form-control"
                          label="totalNWExt"
                          onChange={this.inputChange("totalNWExt")}
                          // onBlur={this.inputGrandTotal("totalNWExt")}
                        />
                      </div>
                    </div>
                  </form>
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-lg-4">
                      <button className="btn btn-warning" onClick={() => this.showGambar()}>
                            Tambah Gambar
                      </button>
                    </div>
                    <div className="col-lg-4 col-md-4 mb-3">
                      <div className="text-center">
                        {this.state.kodeCustomer !== "" ? (
                          <button
                            className="btn btn-primary"
                            data-toggle="modal"
                            type="button"
                            data-target="#barcodeModal"
                          >
                            BARCODE (+)
                          </button>
                        ) : (
                          <button
                            className="btn btn-primary"
                            data-toggle="modal"
                            type="button"
                            data-target="#barcodeModal"
                            disabled
                          >
                            BARCODE (+)
                          </button>
                        )}
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.showTambahBarang()}
                          disabled={this.state.kodeCustomer === ""}
                        >
                          Tambah Barang
                          <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 mt-5">
                  <GlobalTabel
                    keyField="id"
                    data={this.props.ReturnPenjualan}
                    columns={this.state.columns}
                  />
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      disabled={this.props.onSend}
                      onClick={() => this.transaksiPenjualanSubmit()}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
           this.state.showModal !== "showGambar" ?  <Suspense
           fallback={<Skeleton width={"100%"} height={50} count={4} />}
         >
           <FormTransaksiReturn
             onSubmit={(data) => this.handleSubmit(data)}
           />
         </Suspense> : <FormGambarPackingL onSubmit={(data) => this.props.dispatch(hideModal())} />
          }
          title={this.state.showModal !== "showGambar" ? "Tambah Data" : "Tambah Gambar"}
        />
        <div
          id="barcodeModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Bacode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormBarcodeReturnCustomer
                  kodeCustomer={this.state.kodeCustomer}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

ReturnPenjualan = reduxForm({
  form: "ReturnPenjualan",
  enableReinitialize: true,
})(ReturnPenjualan);
export default connect(maptostate, null)(ReturnPenjualan);
