import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const PrintNotaReturn = (
  row1name = "",
  row1isi = "",
  row2name = "",
  row2isi = "",
  row3name = "",
  row3isi = "",
  row4name = "",
  row4isi = "",
  username = "",
  tanggal = "",
  validby = "",
  tableColumn = [],
  title = "",
  tableRows,
  footer = [],
  tableRowsNonAyu = [],
  footerNonAyu = [],
) => {
  // initialize jsPDF
  const doc = new jsPDF("portrait");
  doc.setProperties({
    title: title,
  });
  const logo =
    "data:image/png;base64,";

doc.autoTable({
        head: tableColumn,
        body: tableRows,
        foot: [footer],
        startY: 45,
        theme: "plain",
        rowPageBreak: "avoid",
        pageBreak: "avoid",
        margin: { top: 20 },
        bodyStyles: { lineColor: "#969696",fontSize : 8, lineWidth: 0.05},
        headStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fontSize : 8,
          fillColor: "#E8E8E8",
        },
        footStyles: {
          lineColor: "#969696",
          lineWidth: 0.05,
          fillColor: "#E8E8E8",fontSize : 8,
        },
      });
  let finalY = doc.lastAutoTable.finalY + 10;
 
  if(tableRowsNonAyu.length > 1){
    doc.setFontSize(14);
    doc.text("Catatan pengembalian bukan barang AYU :", 14, finalY + 10);
    doc.setFontSize(10);
    doc.autoTable({
      head: tableColumn,
      body: tableRowsNonAyu,
      foot: [footerNonAyu],
      startY: finalY + 15,
      theme: "plain",
      rowPageBreak: "avoid",
      pageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineColor: "#000",fontSize : 8, },
      headStyles: {
        lineColor: "#000",fontSize : 8,
        fillColor: [212, 212, 211],
      },
      footStyles: {
        fillColor: "#E8E8E8",
      },
    });
  }
  finalY = doc.lastAutoTable.finalY + 7;
  doc.setFontSize(10);
  doc.addImage(logo, "PNG", 14, 15, 35, 12);
  //row 1
  doc.text(row1name, 14, 32);
  doc.text(row1isi === "" ? "" : ":", 37, 32);
  doc.text(row1isi, 40, 32);
  doc.text(row2name, 120, 32);
  doc.text(row2isi === "" ? "" : ":", 141, 32);
  doc.text(row2isi, 145, 32);
  //row 2
  doc.text(row3name, 14, 37);
  doc.text(row3isi === "" ? "" : ":", 37, 37);
  doc.text(row3isi, 40, 37);
  doc.text(row4name, 120, 37);
  doc.text(row4isi === "" ? "" : ":", 141, 37);
  doc.text(row4isi, 145, 37);
  doc.text("Penerima", 25, finalY + 60);
  doc.text("Pengirim", 95, finalY + 60);
  doc.text("Mengetahui", 160, finalY + 60);
  //Stamp
  doc.text("User", 14, finalY + 10);
  doc.text(":", 30, finalY + 10);
  doc.text(username, 33, finalY + 10);
  doc.text("Cetak", 14, finalY + 15);
  doc.text(":", 30, finalY + 15);
  doc.text(tanggal, 33, finalY + 15);
  // doc.text("Valid", 14, finalY + 20);
  // doc.text(":", 30, finalY + 20);
  // doc.text(validby, 33, finalY + 20);
  // doc.autoPrint();
  // doc.save(`${title}_${dateStr}.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>" +
      title +
      "</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  x.document.close();
};

export default PrintNotaReturn;
